import React from 'react';
import Button from './Button';
import conwayGifPath from '../resources/images/project images/conways_game_of_life.gif'
import sudokuGifPath from '../resources/images/project images/sudoku.gif'
import minigameGifPath from '../resources/images/project images/minigame.gif'
import "../App.css";

function Projects() {
    return (
        <React.Fragment>
            <h3 style={headingStyle}>Conway's game of life</h3>
            <p style={textStyle}>This is a simulation of the famous "Conway's game of life". This project was instrumental in improving my understanding of the JavaScript language.</p>
            <img className='imageStyle' src={conwayGifPath} alt='Interractive "Conways gmae of life" project' />
            <div className='smallSpacing'></div>

            {/* <h3 style={headingStyle}>This website</h3>
            <p style={textStyle}>This website was built from scratch using the react framework. It was my first project built using React, and was of great help in solidifying the core concept of React Components and other aspects of the framework.</p>
            <img className='imageStyle' src={websiteImgPath} alt='Interractive "Conways gmae of life" project' />
            <div className='smallSpacing'></div> */}

            <h3 style={headingStyle}>Auto-solving sudoku game</h3>
            <p style={textStyle}>This is one of my projects built using Python. It was of great help in my understanding of recursion and depth-first-search. It also helped me learn to use the python library "Pygame."</p>
            <img className='imageStyle' src={sudokuGifPath} alt='Interractive "Conways gmae of life" project' />
            <div className='smallSpacing'></div>

            <h3 style={headingStyle}>Mini Arcade game</h3>
            <p style={textStyle}>Built as a part of a Computer Organization course at YorkU, this project was critical to my understanding of the C language, the MIPS Assembler instruction set, and Computer Organisation concepts.</p>
            <img className='imageStyle' src={minigameGifPath} alt='Interractive "Conways gmae of life" project' />
            <a href="https:www.github.com/Stan15" target="_blank" rel="noreferrer">
                <Button text='view more' />
            </a>
        </React.Fragment>
    )
}

const headingStyle = {
    textAlign: 'left',
    margin: '10px'
}

const textStyle = {
    textAlign: 'left',
    margin: '10px'
}

export default Projects;