import React from 'react';
import "../../App.css";

const cardOffset = 45;  // %

function CardContent(props) {
    return (
        <div style={contentContainerStyle}>
            <div className="smallSpacing"></div>

            {/* card */}
            <div className='card' id={props.contentInfo.cardTag} style={props.cardDirection==='right' ? rightCardStyle : leftCardStyle}>
                <h2>{props.contentInfo.cardHeading}</h2>
                <p>{props.contentInfo.cardText}</p>
            </div>

            {/* info */}
            {
                props.contentInfo.content===undefined
                    ? (<React.Fragment></React.Fragment>)
                    : <div style={props.cardDirection==='right' ? rightCardContentStyle : leftCardContentStyle}>
                        {props.contentInfo.content}
                    </div>
            }


            <div className="smallSpacing"></div>
        </div>
    )
}

const contentContainerStyle = {
    display: 'inline-block',
    width: '100%'
}

const rightCardStyle = {
    display: 'inline-block',
    marginRight: `-${ cardOffset }%`,
    marginTop: '15px',
    position: 'relative',
    float: 'right'
}

const leftCardStyle = {
    display: 'inline-block',
    marginLeft: `-${ cardOffset }%`,
    marginTop: '15px',
    position: 'relative',
    float: 'left'
}

const leftCardContentStyle = {
    display: 'inline-block',
    margin: 0,
    height: 'fit-content',
    border: 'solid 2px transparent',
    borderRadius: '0.7em',
    backgroundClip: 'padding-box',
    background: '#ffffff77',
    width: '70%'
}

const rightCardContentStyle = {
    display: 'inline-block',
    height: 'fit-content',
    margin: 0,
    padding: '10px',
    border: 'solid 2px transparent',
    borderRadius: '0.7em',
    backgroundClip: 'padding-box',
    background: '#ffffff55',
    width: '70%'
}

export default CardContent;