import React from 'react';
import Button from './Button';
import Projects from './Projects';
import Skills from './Skills';
import {v4 as uuid} from "uuid"; 
import resumeImgPath from '../resources/images/resume.jpg';
import resumePath from '../resources/Resume.pdf';
import "../App.css";

const page_contents = [
    {
        id: uuid(),
        cardTag: 'about',
        cardHeading: 'About me',
        cardText: "Hi! I'm a Software Engineering student in my second year of study at York University, Canada.",
        
    },
    {
        id: uuid(),
        cardTag: 'skills',
        cardHeading: 'Skills',
        cardText: "I have a passion for self learning, thus a large majority of the technologies I have learnt happen to be self taught. Here are a few of those skills and areas where i have practically applied them.",
        content: <Skills />
    },
    {
        id: uuid(),
        cardTag: 'projects',
        cardHeading: 'My projects',
        cardText: 'Being someone who employs a "hands-on" approach to learning, I apply each new skill I learn to building a tangible and practical project. Besides this website, here are a few other projects I have worked on and their impact on my personal growth.',
        content: <Projects />
    },
    {
        id: uuid(),
        cardTag: 'resume',
        cardHeading: 'Resume',
        cardText: 'My resume holds a more comprehensive view of my past work experiences and education. Click on the "view" button below to have a look.',
        content: (
            <React.Fragment>
                <div ></div>
                <img className='imageStyle' src={resumeImgPath} alt='My resume' />
                <a href={resumePath} target='_blank' rel='noreferrer'><Button text='view' /></a>
            </React.Fragment>
        )
    }
]


export default page_contents;