import React from 'react';
import linkedinIcon from '../../resources/images/icons/linkedinIcon.png';
import githubIcon from '../../resources/images/icons/githubIcon.png';
import "../../App.css";

function ContactCard() {
    return (
        <div style={contactContainerStyle}>
            <div style={contactCardStyle} id="contact">
                <h2 style={contactCardHeadingStyle}>Contact</h2>
                <p>You can contact me via the following media.</p>
                <table style={tableStyle}>
                    <tr style={{backgroundColor: 'red'}}>
                        <td>Phone: <br/>
                            <a href="tel:+1-647-472-9370"><p style={textStyle}>(647) 472 9370</p></a>
                        </td>
                        <td style={socialsListStyle}>
                            <a href="https:linkedin.com/in/stanihe1/" target="_blank" rel="noreferrer">
                                <ul style={socialsListStyle}>
                                    <li><img src={linkedinIcon} className='icon' alt='LinkedIn icon'/></li>
                                    <li><h4>stanihe1</h4></li>
                                </ul>
                            </a>
                        </td>
                    </tr>
                    <tr>
                        <td>Email: <br/>
                            <a href="mailto:stanihe1901@gmail.com"><p style={textStyle}>stanihe1901@gmail.com</p></a>
                        </td>
                        <td>
                            <a href="https:github.com/Stan15" target="_blank" rel="noreferrer">
                                <ul style={socialsListStyle}>
                                    <li><img src={githubIcon} className='icon' alt='GitHub icon'/></li>
                                    <li><h4>stan15</h4></li>
                                </ul>
                            </a>
                        </td>
                    </tr>
                </table>
            </div>
        </div>
    )
}

const tableStyle = {
    width: '100%',
    padding: 0
}

const socialsListStyle = {
    float: 'left',
    listStyle: 'none',
    margin: '0',
    display: 'inline',
    width: 'fit-content',
    whiteSpace: 'nowrap'
}

const contactContainerStyle = {
    bottom: 0,
    margin: 0,
    textAlign: 'center',
}

const contactCardStyle = {
    display: 'inline-block',
    textAlign: 'left',
    marginLeft: '10%',
    marginBottom: '20px',
    padding: '2em',
    height: '180px',
    width: '400px',
    backgroundColor: 'transparent',
    border: '#cccccc solid 1px',
    borderRadius: '0.7em'
}

const contactCardHeadingStyle = {
    marginTop: '-55px',
    marginLeft: '-15%',
    left: '30%',
    textAlign: 'center',
    fontSize: '2em',
    fontWeight: '700',
    color: '#505050'
}

const textStyle = {
    fontSize: '10pt',
    padding: 0, 
    margin: 0
}

export default ContactCard;