import React from 'react';
import PageContent from './PageContents';
import "../../App.css";
import page_contents from '../page_contents';


function BodyCard() {
    return (
        <div style={bodyContainerStyle}>
            <div id='bodyCard' style={bodyCardStyle}>
                <div className='mediumSpacing'></div>
                <div style={titleStyle}>
                    <p style={mainTitleStyle}>Stanley Ihesiulo</p>
                    <p style={subTitleStyle}>Software Engineering Student</p>
                </div>
                <div className='largeSpacing'></div>
                <PageContent contents={page_contents}/>
            </div>
        </div>
    )
}

const bodyContainerStyle = {
    textAlign: 'center',
    width: '100%',
    margin: 'auto'
}

const bodyCardStyle = {
    display: 'inline-block',
    marginLeft: '-10%',
    background: '#0060ff16',
    width: '65%',
    padding: '2em',
    borderRadius: '0.7em'
}

const titleStyle = {
    display: 'inline-block',
    marginLeft: '10%',
    color: '#707070',
    whiteSpace: 'nowrap',
    lineHeight: 0,
    padding: 0
}

const mainTitleStyle = {
    fontFamily: 'Playfair Display',
    fontWeight: '400',
    fontSize: '4.5em'
}

const subTitleStyle = {
    fontFamily: 'Lato',
    fontWeight: '300',
    fontSize: '1.5em',
}

export default BodyCard;