import React from 'react';
import CardContent from './CardContent';
import "../../App.css";

function PageContents(props) {
    return (
        props.contents.map((contentInfo, index) => (
            <React.Fragment>
                <CardContent contentInfo={contentInfo} cardDirection={index%2===0 ? 'right' : 'left'} />
            </React.Fragment>
        ))
    )
}

export default PageContents;